@import "../../style/breakpoints.scss";

.header {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  @include md {
    justify-content: space-between;
  }
  padding: 0px 10px;
  border-radius: 30px 30px 0 0;
  position: relative;
  z-index: 999;
  height: 50px;
  &.elevated {
    box-shadow: 0 1px 0.5rem rgba(0, 0, 0, 0.3);
  }
}
